.page-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  align-content: flex-end;
  flex-wrap: wrap;
  padding: 0 40px;
}
.page-content .content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  gap: 60px;
  padding: 60px 0;
  width: 100%;
}
@media (min-width: 992px) {
  .page-content {
    padding: 0 60px;
    justify-content: flex-end;
  }
  .page-content .content {
    flex-direction: row;
    max-height: 600px;
    padding: 100px 0;
  }
}
@media (min-width: 1200px) {
  .page-content {
    padding: 0 165px;
  }
}
